/* General styling */
body {
  font-family: Arial, sans-serif;
  margin: 2rem;
}

h1 {
  text-align: center;
  color: #2774c7;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
}

hr {
  border: 1px solid #ddd;
  /* color: #ccc; */
  margin: 2rem 0;
}
/* Search input and button styling */
input[type="text"] {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 1rem;
  font-size: 1.2rem;
}

button {
  padding: 0.5rem 1rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 1.2rem;
}

button:hover {
  background-color: #0056b3;
}

/* Table styling */
table {
  border: 1px solid #ddd;
  width: 100%;
  margin-top: 2rem;
  border-collapse: collapse;
}

th,
td {
  padding: 0.5rem;
  border-bottom: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

th:last-child,
td:last-child {
  border-right: none;
}

th {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #f5f5f5;
}

.vendor-search-container {
  max-width: 800px;
  margin: 0 auto;
}
